.event {
	display: flex;
	align-items: center;
	justify-items: center;
	flex-direction: column;
	padding: 20px 20px 45px; /* Adjusted padding */
	margin-bottom: 5px; /* Adjusted margin */
  border: 1px solid #ccc;
  border-radius: 10px;
  position: relative;

}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

.btnSubmit {
	display: inline-block;
	padding: 9px 20px;
	font-size: 16px;
	font-weight: bold;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	border: 1px solid #4caf50; 
	color: #4caf50;
	background-color: #ffffff; 
	border-radius: 5px;
	transition: background-color 0.3s, color 0.3s, border-color 0.3s;
}
.btnError{display: inline-block;
	padding: 9px 20px;
	font-size: 16px;
	font-weight: bold;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	border: 1px solid #d4da3a; 
	color: #ffffff;
	background-color: #000000; 
	border-radius: 5px;
	transition: background-color 0.3s, color 0.3s, border-color 0.3s;}

.btnSubmit:hover {
	background-color: #4caf50; 
	color: #ffffff;
	border-color: #ffffff; 
}

.error-message {
	color: #ba3939;
  background: #ffe0e0;
  border: 1px solid #a33a3a;

}

.alerts-container {
  position: fixed;
  top: 0px;
  left: 20px;
  width: 250px;
  z-index: 1000;
}